.nav,
.pagination,
.carousel,
.panel-title a {
  cursor: pointer;
}
.blue-header {
  color: blue;
  display: inline-block;
}
.plus-icon {
  width: 40px;
  height: 40px;
  background: red;
  padding: 10px;
  border-radius: 100%;
  color: white;
  text-align: center;
}
.remove-icon {
  width: 20px;
  height: 20px;
  background: red;
  padding: 2px;
  border-radius: 100%;
  color: white;
  text-align: center;
  cursor: pointer;
}
.clickable,
.plus-icon {
  cursor: pointer;
}
.control-label.required:after {
  content: " *";
  color: red;
}
.form-control.ng-dirty.ng-invalid {
  border-color: red;
}
.padding-left-5 {
  padding-left: 5px;
}
.ng-cloak,
.hidden {
  display: none;
}
.alert-dismissible .close {
  position: relative;
  top: -2px;
  right: 0px;
  color: inherit;
}
.modal-content,
.modal-form {
  width: 700px;
}
.modal-footer {
  border-top: none;
}
.main-item {
  min-height: 200px;
}
#ui-overlay {
  position: absolute;
  top: 50%;
  left: 50%;
  margin: -50px 0px 0px -50px;
}
